import * as types from '../action-types';

/**???????????????????????????reducer*/
let initState = {
    //????????????????????????
    list:[],//?????? new ??????
    loading: '',//???????????? ?????????
    hasMore: true,//?????????????????????
    slider: [],//???????????????
    friends:[],//?????? ????????? ??????
    offset: 0, //?????????
    limit: 5   //???????????????

};
export default function (state = initState, action) {
    //??????action??????????????????????????????payload????????????????????????????????????
    switch (action.type) {//?????????????????????
        case types.GET_HLIST:
            return {
                ...state,
                ...action.payload,
            };
            case types.GET_FRIEND:
            return {
                ...state,
                friends:[...action.payload]
            };
        default:
            return state;
    }
}


