import React,{Component} from 'react';

export default class Cover extends Component{
    constructor(){
        super();
        this.state = {
            isCover:false
        }
    };
    cover=()=>{
        this.setState({isCover:!this.state.isCover});
    };
    render(){
        return (
            <div className="cover-all">
                <div onClick={this.cover} className="tab-plus"><div/><span/></div>
                {
                    this.state.isCover?<div className="cover-all"/>:""
                }
            </div>
        )
    }
}



