import * as types from '../action-types';
let initState = {
    //?????????????????????????????????
    isLoading: '',//???????????? ?????????
    keyword:'',//???????????????
    limit: 3 ,  //???????????????
    searchList: [],  //???????????????????????????????????????
    line:'',
    star: {},
};
export default function (state = initState, action) {
    //??????action??????????????????????????????payload????????????????????????????????????
    switch (action.type) {//?????????????????????
        case types.FETCH_SEARCH://???lesson??????????????????
            return {
                ...state,
                ...action.payload,
            };
        case types.FETCH_CLASSIFY:
            return {
                ...state,
                searchList:[...action.payload.data.find_recipe],
            };
        case types.GET_STARFOOD:
            console.log(action.payload);
            return{
                ...state,
                star: {...action.payload}
            };
        default:
            return state;
    }
}


