import * as types from '../action-types'
import {fetchLesson, fetchLessonPrefer} from '../../api/lession'
export default {
    //??????????????????????????????
    getLessons(){
        //??????fetch????????????????????????????????????then????????????????????????????????????????????????????????????????????????,?????????????????????????????????
        return dispatch => {
            fetchLesson().then(value => {
                dispatch({
                    type: types.FETCH_LESSONS,
                    payload: value
                })
            })
        }
    },
    refresh(){
        return (dispatch, getState) => {
            //??????????????????,???????????????payload?????????????????????????????????,????????????????????????promise,?????????promise??????,??????????????????????????????action.??????action???payload????????????promise???resolve??????
            let {loading, hasMore, offset, limit} = getState();
            //???????????????????????????,???????????????????????????????????????????????????????????????
            if (!loading) {
                //????????????action,????????????????????????
                dispatch({
                    type: types.FETCH_LESSONS
                });
                dispatch({
                    type: types.FETCH_LESSONS_REFRESH,
                    payload: fetchLesson()
                })
            }

        }
    },
    getLessonPrefer(){
        return dispatch => {
            fetchLessonPrefer().then(value => {
                dispatch({
                    type: types.FETCH_LESSON_PREFER,
                    payload: value
                })
            })
        }
    }
}



