import * as types from '../action-types';
import {getHList,getFriend} from '../../api/home';

export default {
    getHList() {//?????????????????????&&??????
        return dispatch => {
            getHList().then( payload=> {
                dispatch({
                    type: types.GET_HLIST,
                    payload
                });
            });
        }
    },
    getFriend() {//???????????? ????????? ??????
        return dispatch => {
            getFriend().then( payload=> {
                dispatch({
                    type: types.GET_HLIST,
                    payload
                });
            });
        }
    },

}


